body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  max-width: 800px;
  margin: auto;
  background: white;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.input-group {
  margin-bottom: 20px;
}

.input-group label {
  display: block;
  margin-bottom: 5px;
}

.input-group input,
.input-group select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.checkbox-group {
  margin: 20px;
  display: flex;
  justify-content: space-between;
}

.checkbox-group label {
  margin-left: 5px;
}

.btn-calculate {
  margin-top: 20px;
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.btn-calculate:hover {
  background-color: #0056b3;
}

.result {
  margin-top: 20px;
  padding: 10px;
  background-color: #dff0d8;
  border: 1px solid #d0e9c6;
  color: #3c763d;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

table, th, td {
  border: 1px solid #ddd;
}

th, td {
  text-align: left;
  padding: 8px;
}

th {
  background-color: #f2f2f2;
}

tr:nth-child(even) {
  background-color: #f9f9f9;
}

button {
  background-color: #ff4d4d;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

button:hover {
  background-color: #cc0000;
}

.flex-container {
  display: flex;
  justify-content: space-between; /* Adjust this as needed for your layout */
}

.flex-item {
  flex: 1; /* Allows items to grow and take up equal space */
  padding: 0 10px; /* Adds some spacing between items */
}

.flex-item:first-child,
.flex-item:last-child {
  padding-left: 0;
  padding-right: 0;
}

.tab-navigation button {
  background-color: #aa3c3c;
  border: none;
  padding: 10px 20px;
  margin-right: 5px;
  cursor: pointer;
}

.tab-navigation button:hover {
  background-color: #192a8b;
}

.tab-navigation button.active {
  background-color: #184ad4;
  color: #e2e2e2;
}